<template>
	<view class="goods-search-list">
		<search-header v-model="keywords" placeholder="输入货物信息查询" confirmText="确定" @confirm="onConfirm" @input="onInputWords"></search-header>
		
		<template v-if="showSearch">
			<view v-show="showSearch" class="search-words-cover">
				<scroll-view class="search-words-list" scroll-y>
					<view v-for="(item,index) in searchList" :key="index" class="search-item" @click="onClickSearchItem(index)">
						<text>{{ item.text }}</text>
					</view>
				</scroll-view>
			</view>
		</template>
		
		<template v-else>
			<view class="history-info">
				<view class="history-title">
					<text class="title">历史记录</text>
					<text class="clear-btn">清除记录</text>
				</view>
				<view class="history-list">
					<view v-for="(item,index) in historyItemList" :key="index" class="history-item" @click="onClickHistory(index)">
						<text>{{ item.text }}</text>
					</view>
				</view>
			</view>
		</template>
		
	</view>
</template>

<script>
	import SearchHeader from '@/components/search-header/index.vue'
	export default {
		components: {
			SearchHeader
		},
		data() {
			return {
				keywords: '',
				showSearch: false,
				historyItemList: [
					{
						text: '砂石'
					}
				],
				searchList: [
					{
						text: '灯具配件'
					},
					{
						text: '灯具'
					},
					{
						text: '灯具配件'
					},
					{
						text: '灯具'
					},
					{
						text: '灯具配件'
					},
					{
						text: '灯具'
					},
					{
						text: '灯具配件'
					},
					{
						text: '灯具'
					},
				]
			}
		},
		methods: {
			onInputWords() {
				this.showSearch = true
			},
			onConfirm(val) {
				this.showSearch = false
			},
			onClickHistory(index) {
				const hostoryItem = this.historyItemList[index]
				this.keywords = hostoryItem.text
			},
			onClickSearchItem(index) {
				const searchItem = this.searchList[index]
				this.keywords = searchItem.text
				setTimeout(() => {
					this.showSearch = false
				}, 0)
			}
		}
	}
</script>

<style lang="scss" scoped>
	.goods-search-list {
		min-height: 100%;
		background-color: #fff;
		padding: 20upx 30upx;
		box-sizing: border-box;
	}
	
	.history-info {
		margin-top: 40upx;
	}
	
	.history-title {
		@include flex-c-center;
		justify-content: space-between;
		
		.title {
			font-weight: 500;
		}
		
		.clear-btn {
			color: #999999;
		}
	}
	
	.history-list {
		margin-top: 20upx;
	}
	
	.history-item {
		display: inline-block;
		vertical-align: top;
		line-height: 64upx;
		padding: 0 30upx;
		background-color: #F6F6F6;
		border-radius: 10upx;
		margin-bottom: 20upx;
		transition: all .3s;
		
		&:hover {
			background-color: $theme-color;
			color: #fff;
		}
		
		& + .history-item {
			margin-left: 20upx;
		}
	}
	
	.search-words-cover {
		position: absolute;
		top: 92upx;
		left: 30upx;
		right: 30upx;
		background-color: #fff;
	}
	
	.search-words-list {
		max-height: 800upx;
	}
	
	.search-item {
		padding: 30upx 0;
		border-bottom: 2upx solid $border-color;
		font-size: 32upx;
	}
</style>
